import React from 'react'
import Last from '../images/Firstbank-Last-Pic.png'
import Second from '../images/FirstBank-Second-Pic.png'
import Third from '../images/FirstBank-Third-Pic.png'

const Bottom = () => {
    return (
        <>
        <div className="flex justify-center items-center">
            <div>
                <img src={Second} alt="" className=""/>
            </div>
            <div>
                <img src={Last} alt="" className=""/>
            </div>
            <div>
                <img src={Third} alt="" className=""/>
            </div>
        </div>
        </>
    )
}

export default Bottom
