import React from 'react'
import FirstBank from '../images/BankLogo.png'
import Visa from '../images/VisaLogo.png'

const Footer = () => {
    return (
        <div>
            <div className="flex justify-between pt-10">
              <img src={FirstBank} alt="" className="pl-20"/>  
              <img src={Visa} alt="" className="pr-20"/>
            </div>
            <h1 className="p-20 font-bold tracking-wider">*Visa's Zero Liability Policy does not apply to certain commercial card and anonymous prepaid card transactions or transaction or transactions not processed by Visa. Cardholders must use car in protection their card and notify their issuing financial institution immediately of any unauthorized use. Contact your Issuer for more detail.</h1>
        </div>
    )
}

export default Footer
